.has-icon-arrow-right-alt .wp-block-button__link::before,
.has-icon-arrow-right-alt .wp-block-button__link::after {
  display: none;
  font-family: 'dashicons';
  font-size: 20px;
  vertical-align: middle; }

.has-icon-arrow-right-alt.has-left-icon .wp-block-button__link::before {
  display: inline-block;
  content: "";
  padding-right: 8px; }

.has-icon-arrow-right-alt.has-right-icon .wp-block-button__link::after {
  position: relative;
  display: inline-block;
  content: "";
  right: 0;
  padding-left: 8px;
  transition: right .25s ease-in-out; }

.has-icon-arrow-right-alt:hover .wp-block-button__link::after {
  right: -6px; }

