.noor-block-bullet-point {
  display: flex;
  width: 100%; }
  .noor-block-bullet-point.has-align-center {
    justify-content: center; }
  .noor-block-bullet-point.has-align-right {
    justify-content: flex-end; }
  .noor-block-bullet-point.has-align-left {
    justify-content: flex-start; }
  .noor-block-bullet-point__point {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 87px;
    height: 87px;
    border-radius: 50%;
    overflow: hidden; }
  .noor-block-bullet-point__text-content {
    margin: 0 !important; }

.noor-block-google-map {
  position: relative;
  height: 0;
  overflow: hidden; }
  .noor-block-google-map iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    border: 0 !important; }

.noor-block-accordion {
  display: flex;
  align-items: center;
  padding: 0 2rem;
  margin: 1rem auto 0 !important; }
  .noor-block-accordion__title {
    width: 100%;
    margin: 0.5rem 0 !important; }
  .noor-block-accordion__icon[data-state="true"]  {
    display: block; }
  .noor-block-accordion__icon[data-state="false"] {
    display: none; }
  .noor-block-accordion__content {
    height: 0;
    padding: 0 2rem;
    overflow: hidden; }
  .noor-block-accordion[aria-expanded="true"] + .noor-block-accordion__content {
    height: auto; }
